var VueMarkdown = require('vue-markdown');


const colors = {
  'material': {
    'red': '#F44336',
    'pink': '#E91E63',
    'purple': '#9C27B0',
    'deep-purple': '#673AB7',
    'indigo': '#3F51B5',
    'blue':  '#2196F3',
    'light-blue': '#03A9F4',
    'cyan': '#00BCD4',
    'teal': '#009688',
    'green': '#4CAF50',
    'light-green': '#8BC34A',
    'lime': '#CDDC39',
    'yellow': '#FFEB3B',
    'amber': '#FFC107',
    'orange': '#FF9800',
    'deep-orange': '#FF5722',
    'brown': '#795548',
    'grey': '#9E9E9E',
    'blue-grey': '#607D8B',
    'black': '#000',
    'white': '#fff'
  }
}


// const colors_css = {
//   'material': 

// }

export default {
