    selected: 'material',
    options: {
      'ReadOnly': "true"
    }
    }
  },

  computed: {
    // selected_css() {
    //   for(let key of Object.keys(colors[this.selected])) {
         
    //   }
    //   return String(colors[this.selected]);
    // },

    // selected_html() {
    //   return '';
    // }
  },
  mounted() {
    this.selectItem('material');
  },

  methods: {
    selectItem(name) {
      this.selected = name;
      // this.contentcss = colors_css[name];
      this.contenthtml = '';
      this.readText();
    },
